import ArticleCard from "./ArticleCard";

function sortByDate(items) {
  return items.sort((a, b) => {
    if (a.date < b.date) return 1;
    if (a.date > b.date) return -1;
    return 0;
  });
}

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('AuthorView', component.exports)
}
export default {
  components: {
    ArticleCard
  },
  props: {
    item: Object,
    getArticleInfo: Function
  },
  data() {
    return {
      articles: [],
      articleIds: []
    };
  },
  computed: {
    author() {
      return this.item;
    }
  },
  mounted() {
    const item = this.item;
    this.articleIds = item.articles;
    Promise.all(
      item.articles.map(async el => {
        return await this.getArticleInfo(el._id);
      })
    ).then(articles => {
      this.articles = sortByDate(articles);
    });
  },
  beforeUpdate() {
    const item = this.item;
    if (this.articleIds !== item.articles) {
      this.articleIds = item.articles;
      Promise.all(
        this.item.articles.map(async el => {
          return await this.getArticleInfo(el._id);
        })
      ).then(articles => {
        this.articles = sortByDate(articles);
      });
    }
  }
};
