if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('BaseToolbar', component.exports)
}
export default {
  props: {
    menu: Boolean
  },
  data() {
    return {
      height: 60,
      title: "Research Hub",
      views: ["about", "apps", "articles", "datasets"],
      drawer: null
    };
  },
  computed: {
    titleUpper() {
      return this.title.toUpperCase();
    },
    hpixel() {
      return `${this.height}px`;
    },
    logoHpixel() {
      return `${this.height * 0.6}px`;
    }
  }
};
